
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ccc;
  padding: 10px 20px;
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  width: 70px;
}

.nav-items ul {
  display: flex;
  list-style: none;
}

.nav-items li {
  margin: 0 10px;
  padding: 8px 12px;
}

.res-card {
    width: 200px;
    height: 300px;
    border: 1px solid black;
}

.search {
    padding: 10px;
}
 

